.menu-theme-switcher {
  position: absolute;
  bottom: 20px;
  left: 20px;
}

.switcher-toggle[type="checkbox"] {
  height: 0;
  width: 0;
  visibility: hidden;
  display: none;
}

.check-toggle {
  width: 46px;
  height: 20px;
  background-color: var(--switch-bg-off-color);
  cursor: pointer;
  transition: var(--animation-set);
  border-radius: 18px;
  display: block;
  position: relative;
  box-shadow: 4px 4px 15px rgba(243, 243, 243, .25);
}

.check-toggle:before {
  content: "";
  width: 18px;
  height: 18px;
  background-color: var(--light-background-color);
  transition: var(--animation-set);
  border-radius: 18px;
  position: absolute;
  top: 1px;
  left: 1px;
}

.switcher-toggle:checked + .check-toggle {
  transition: transform var(--animation-set);
  background-color: var(--accent-color);
}

.switcher-toggle:checked + .check-toggle:before {
  left: calc(100% - 1px);
  transform: translateX(-100%);
}

.check-toggle:active:before {
  width: 44px;
}

.backdrop {
  transition: opacity var(--animation-set);
  opacity: 1;
  background: rgba(46, 47, 66, .4);
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.backdrop.is-hidden {
  opacity: 0;
  pointer-events: none;
}

